<template>
    <div id="form">
        <h5>&nbsp;&nbsp;•&nbsp;Basic Details</h5>
        <div >
            <input type="text" id="name" class="input" v-model.trim="itemName" placeholder="Item Name">
            <span v-if="!formValidity">Item name not entered !</span>
        </div>
        <div >
            <input type="number" class="input" v-model.trim="itemPrice" placeholder="Item Price">
            <span v-if="!formValidity">Item price not entered !</span>
        </div>
        <select v-model="itemType" id="itemType" class="form-control input">
            <option class="input" value="Starter">Starter</option>
            <option class="input" value="Main course">Main course</option>
            <option class="input" value="Beverage">Beverage</option>
            <option class="input" value="Desert">Desert</option>
        </select>
        <hr>
        <h5>&nbsp;&nbsp;•&nbsp;Other Details</h5>
        <div id="checkboxes">
            <input style="margin:1vh" type="checkbox" id="special" v-model="isSpecial" hidden="">
            <label class="checkmark" for="special"></label>
            <div>Special</div>
        </div>
        <div id="checkboxes">
            <input  style="margin:1vh" type="checkbox" id="spicy" v-model="isSpicy" hidden="">
            <label class="checkmark" for="spicy"></label>
            <div>Spicy</div>
        </div>
        <hr>
        <div>
            <h5>&nbsp;&nbsp;•&nbsp;Ingredients</h5>
            <ul>
                <li v-for="i in ingredients" :key="i">⇀ &nbsp;{{ i }} <button @click="pop_ingredient(i)" class="btn-red">Remove</button></li>
            </ul>
        </div>
        <div>
            <input type="textbox" class="input" v-model="temp" placeholder="Type ingredient and press enter" v-on:keyup.enter="check_for_enter">
        </div>
        <span v-if="ingreError==true"   style="width:14.4vw" >Cannot be blank !</span>
        <hr>
        <button type="submit" @click="pushItemToDatabase" class="btn-green">Add item</button>
    </div>
</template>
<script>
export default {
    name: "addNewItem",
    data(){
        return{
            itemName:"",
            itemPrice:null,
            itemType:"Starter",
            isSpecial:false,
            isSpicy:false,
            formValidity:true,
            ingredients:[],
            temp:"",
            ingreError:false
        }
    },
    methods:{
        pushItemToDatabase(){
            if(this.itemName=="" || this.itemPrice==null){
                this.formValidity=false;
            }
            else{ 

                fetch('http://localhost/getFoodItems',{
                    method:'POST',
                    headers:{
                        'Content-Type':'application/json'
                    },
                    body: JSON.stringify({
                        itemName:this.itemName,
                        itemPrice:this.itemPrice,
                        itemType:this.itemType,
                        isSpecial:this.isSpecial,
                        isSpicy:this.isSpicy,
                        ingredients:this.ingredients
                    })
                });

                this.formValidity=true;
                this.itemName=""
                this.itemPrice=""
                this.itemType="Starter"
                this.isSpecial=false
                this.isSpicy=false
                this.ingredients=[]
            }
        },
        pop_ingredient(i){
            const index = this.ingredients.indexOf(i);
            if(index != -1){
                this.ingredients.splice(index,1);
            }
        },
        check_for_enter(e){
            
            if(e.key=='Enter'){
                if(this.temp.trim()==""){
                    this.ingreError=true;
                }
                else{
                    let temp = this.temp[0].toUpperCase() + this.temp.slice(1);
                    this.ingredients.push(temp);
                    this.temp="";
                    this.ingreError=false;
                }
            }
        }
    }
}

</script>
<style scoped>
