    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 2vw 0.3vw 2vw;
    margin: 0.9vw 2vw 0.4vw 2vw;
}

.btn:before {
 content: "";
 position: absolute;
 width: 100px;
 height: 120%;
 background-color: #28ac84;
 top: 50%;
 transform: skewX(30deg) translate(-150%, -50%);
 transition: all 0.5s;
}

.btn:hover {
 background-color: #e9be7e;
 color: rgb(17, 13, 13);
 box-shadow: 0 1px 0 1px rgb(0, 0, 0);
 transform:scale(1.1);
}
.btn:hover::before {
 transform: skewX(30deg) translate(300%, -50%);
}

.btn:active {
 background: rgb(135, 204, 175);
 transform: scale(0.9);
}
.checkmark {
 display: block;
 width: 30px;
 height: 30px;
 background-color: #ddd;
 border-radius: 10px;
 position: relative;
 transition: background-color 0.4s;
 overflow: hidden;
 cursor: pointer;
}

#special:checked ~ .checkmark {
 background-color: #08bb68;
}
#spicy:checked ~ .checkmark {
 background-color: #08bb68;
}

.checkmark::after {
 content: "";
 position: absolute;
 width: 5px;
 height: 10px;
 border-right: 3px solid #fff;
 border-bottom: 3px solid #fff;
 top: 44%;
 left: 50%;
 transform: translate(-50%, -50%) rotateZ(40deg) scale(10);
 opacity: 0;
 transition: all 0.4s;
}

#special:checked ~ .checkmark::after {
 opacity: 1;
 transform: translate(-50%, -50%) rotateZ(40deg) scale(1);
}
#spicy:checked ~ .checkmark::after {
 opacity: 1;
 transform: translate(-50%, -50%) rotateZ(40deg) scale(1);
}
