/* Button */
.btn-blue {
    
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #8faaa6;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 2vw 0.3vw 2vw;
    letter-spacing: 0.1vw;
}

.btn-blue:before {
 content: "";
 position: absolute;
 width: 100px;
 height: 120%;
 background-color: #674c86;
 top: 50%;
 transform: skewX(30deg) translate(-150%, -50%);
 transition: all 0.5s;
}

.btn-blue:hover {
 background-color: #639df3;
 color: rgb(255, 255, 255);
 box-shadow: 0 1px 0 1px rgb(0, 0, 0);
 transform:translateY(-2px);
}
.btn-blue:hover::before {
 transform: skewX(30deg) translate(300%, -50%);
}

.btn-blue:active {
